.popup__title {
  display: flex;
  max-width: 330px;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  margin: 0 0 42px 0;
}

@media screen and (max-width: 768px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
    max-width: 248px;
    margin: 0 0 70px 0;
  }
}
